
@media (min-width: 768px) and (max-width: 1023px) {
.fitcontent-sm {
    width: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
}
}
@media (max-width: 768px) {
.fitcontent-xs {
    width: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
}
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 800px;
  padding-top: 40px;
  padding-right: 25px;
  padding-left: 25px;
  font-size: 15px;
  background-image: url(./assets/white-background.png);
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 768px) {
#app {
      -ms-word-break: break-all;
      word-break: break-all;
      /* Non standard for webkit */
      word-break: break-word;
      -webkit-hyphens: auto;
      -ms-hyphens: auto;
      hyphens: auto;
      padding-right: 0px;
      padding-left: 0px;
}
}
body {
  margin: 0;
  background-image: url(./assets/gray-background.png);
}



