    border-radius: 3px;
}
@media (max-width: 768px) {
.resume .header-info img {
        margin-right: auto;
        margin-left: auto;
        display: block;
        top: -20px;
}
}
.resume .header-info .left-side {
    padding-right: 1em;
}



